'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { PhoneInput } from '@/components/ui/phone-input';
import { LoadingSpinner } from '@/components/ui/loading-spinner';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from '@/components/ui/form';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { ArrowLeft, CheckCircle, Mail, PawPrint, Smartphone, Sparkles } from 'lucide-react';
import { toast } from 'sonner';
import { authClient } from '@/lib/auth-client';
import { OTPInputDialog } from '@/components/auth/otp-input-dialog';

// Types
type ResetMethod = 'email-otp' | 'sms-otp';
type ContactType = 'email' | 'phone';
type Step = 'contact' | 'otp-sent' | 'new-password' | 'success';

// Validation schemas
const emailSchema = z.object({
  email: z.string().email('   '),
});

const phoneSchema = z.object({
  phoneNumber: z.string().min(10, '  '),
});

const passwordSchema = z
  .object({
    password: z
      .string()
      .min(8, '  8  ')
      .regex(/[a-z]/, '  ')
      .regex(/[A-Z]/, '  ')
      .regex(/[0-9]/, '  '),
    confirmPassword: z.string(),
  })
  .refine((data) => data.password === data.confirmPassword, {
    message: '  ',
    path: ['confirmPassword'],
  });

type EmailInput = z.infer<typeof emailSchema>;
type PhoneInputType = z.infer<typeof phoneSchema>;
type PasswordInput = z.infer<typeof passwordSchema>;

/**
 * Forgot Password Page - OTP-Only Implementation
 *
 * Supports two password reset methods:
 * 1. Email OTP: 6-digit code via email
 * 2. SMS OTP: 6-digit code via phone number
 *
 * All methods use better-auth plugins directly (no custom API routes)
 */
export default function ForgotPasswordPage() {
  const router = useRouter();

  // Step management
  const [step, setStep] = useState<Step>('contact');
  const [isLoading, setIsLoading] = useState(false);

  // Contact type
  const [contactType, setContactType] = useState<ContactType>('email');

  // User contact info
  const [email, setEmail] = useState('');
  const [phoneNumber, setPhoneNumber] = useState('');

  // Method (determined by contact type)
  const [selectedMethod, setSelectedMethod] = useState<ResetMethod | null>(null);

  // OTP handling
  const [showOTPDialog, setShowOTPDialog] = useState(false);
  const [verifiedOTP, setVerifiedOTP] = useState('');

  // Forms
  const emailForm = useForm<EmailInput>({
    resolver: zodResolver(emailSchema),
    defaultValues: { email: '' },
  });

  const phoneForm = useForm<PhoneInputType>({
    resolver: zodResolver(phoneSchema),
    defaultValues: { phoneNumber: '' },
  });

  const passwordForm = useForm<PasswordInput>({
    resolver: zodResolver(passwordSchema),
    defaultValues: { password: '', confirmPassword: '' },
  });

  /**
   * Step 1: Submit email
   */
  const handleEmailSubmit = async (data: EmailInput) => {
    setEmail(data.email);
    setSelectedMethod('email-otp');
    setIsLoading(true);

    try {
      await authClient.emailOtp.sendVerificationOtp({
        email: data.email,
        type: 'forget-password',
      });

      toast.success('  ');
      setShowOTPDialog(true);
    } catch (error: any) {
      console.error('Send email OTP error:', error);
      toast.error(error?.message || ' .  ');
    } finally {
      setIsLoading(false);
    }
  };

  /**
   * Step 1: Submit phone number
   */
  const handlePhoneSubmit = async (data: PhoneInputType) => {
    setPhoneNumber(data.phoneNumber);
    setSelectedMethod('sms-otp');
    setIsLoading(true);

    try {
      await authClient.phoneNumber.sendOtp({
        phoneNumber: data.phoneNumber,
      });

      toast.success('  ');
      setShowOTPDialog(true);
    } catch (error: any) {
      console.error('Send SMS OTP error:', error);
      toast.error(error?.message || ' .  ');
    } finally {
      setIsLoading(false);
    }
  };

  /**
   * Step 2: OTP verified, show password form
   */
  const handleOTPSuccess = (otp?: string) => {
    if (!otp) {
      toast.error('  ');
      return;
    }
    setVerifiedOTP(otp);
    setShowOTPDialog(false);
    setStep('new-password');
    toast.success(' .   ');
  };

  /**
   * Step 3: Reset password with verified OTP
   */
  const handlePasswordSubmit = async (data: PasswordInput) => {
    if (!verifiedOTP) {
      toast.error(' ');
      return;
    }

    setIsLoading(true);

    try {
      if (selectedMethod === 'email-otp') {
        // Email OTP password reset
        const { error } = await authClient.emailOtp.resetPassword({
          email,
          otp: verifiedOTP,
          password: data.password,
        });

        if (error) {
          toast.error(error.message || '  ');
          setIsLoading(false);
          return;
        }
      } else if (selectedMethod === 'sms-otp') {
        // SMS OTP password reset
        const { error } = await authClient.phoneNumber.resetPassword({
          phoneNumber,
          otp: verifiedOTP,
          newPassword: data.password,
        });

        if (error) {
          toast.error(error.message || '  ');
          setIsLoading(false);
          return;
        }
      }

      // Success!
      setStep('success');
      toast.success('  ');

      // Redirect to sign-in after 2 seconds
      setTimeout(() => {
        router.push('/auth/signin');
      }, 2000);
    } catch (error) {
      console.error('Password reset error:', error);
      toast.error('    ');
      setIsLoading(false);
    }
  };

  return (
    <div className="bg-background flex min-h-screen items-center justify-center p-4">
      <div className="w-full max-w-md">
        {/* Header */}
        <div className="mb-8 text-center">
          <div className="mb-4 flex items-center justify-center space-x-2">
            <Sparkles className="text-primary h-8 w-8" />
            <PawPrint className="text-primary h-8 w-8" />
          </div>
          <h1 className="text-primary text-2xl font-bold"></h1>
          <p className="text-muted-foreground">   </p>
        </div>

        <Card>
          <CardHeader className="text-center">
            <CardTitle> </CardTitle>
            <CardDescription>
              {step === 'contact' && '     '}
              {step === 'new-password' && '  '}
              {step === 'success' && ' '}
            </CardDescription>
          </CardHeader>

          <CardContent>
            {/* Step 1: Contact Input */}
            {step === 'contact' && (
              <Tabs
                value={contactType}
                onValueChange={(value) => setContactType(value as ContactType)}
                className="w-full"
              >
                <TabsList className="grid w-full grid-cols-2">
                  <TabsTrigger value="email">
                    <Mail className="mr-2 h-4 w-4" />
                    
                  </TabsTrigger>
                  <TabsTrigger value="phone">
                    <Smartphone className="mr-2 h-4 w-4" />
                    
                  </TabsTrigger>
                </TabsList>

                {/* Email Tab */}
                <TabsContent value="email" className="space-y-4">
                  <Form {...emailForm}>
                    <form
                      onSubmit={emailForm.handleSubmit(handleEmailSubmit)}
                      className="space-y-6"
                    >
                      <FormField
                        control={emailForm.control}
                        name="email"
                        render={({ field }) => (
                          <FormItem>
                            <FormLabel> </FormLabel>
                            <FormControl>
                              <Input
                                type="email"
                                placeholder="example@email.com"
                                disabled={isLoading}
                                autoComplete="email"
                                {...field}
                              />
                            </FormControl>
                            <FormDescription>
                                   
                            </FormDescription>
                            <FormMessage />
                          </FormItem>
                        )}
                      />

                      <Button type="submit" className="w-full" disabled={isLoading}>
                        {isLoading && <LoadingSpinner size="sm" className="mr-2" />}
                        <Mail className="mr-2 h-4 w-4" />
                          
                      </Button>
                    </form>
                  </Form>
                </TabsContent>

                {/* Phone Tab */}
                <TabsContent value="phone" className="space-y-4">
                  <Form {...phoneForm}>
                    <form
                      onSubmit={phoneForm.handleSubmit(handlePhoneSubmit)}
                      className="space-y-6"
                    >
                      <FormField
                        control={phoneForm.control}
                        name="phoneNumber"
                        render={({ field }) => (
                          <FormItem>
                            <FormLabel></FormLabel>
                            <FormControl>
                              <PhoneInput
                                defaultCountry="KR"
                                placeholder="010-1234-5678"
                                disabled={isLoading}
                                value={field.value}
                                onChange={field.onChange}
                              />
                            </FormControl>
                            <FormDescription>
                                  
                            </FormDescription>
                            <FormMessage />
                          </FormItem>
                        )}
                      />

                      <Button type="submit" className="w-full" disabled={isLoading}>
                        {isLoading && <LoadingSpinner size="sm" className="mr-2" />}
                        <Smartphone className="mr-2 h-4 w-4" />
                          
                      </Button>
                    </form>
                  </Form>
                </TabsContent>
              </Tabs>
            )}

            {/* Step 2: OTP Dialog (modal) */}
            <OTPInputDialog
              open={showOTPDialog}
              onOpenChange={setShowOTPDialog}
              identifier={contactType === 'email' ? email : phoneNumber}
              method={contactType === 'email' ? 'email' : 'sms'}
              type="forget-password"
              onSuccess={handleOTPSuccess}
            />

            {/* Step 3: New Password Input */}
            {step === 'new-password' && (
              <Form {...passwordForm}>
                <form
                  onSubmit={passwordForm.handleSubmit(handlePasswordSubmit)}
                  className="space-y-6"
                >
                  <Alert className="border-green-200 bg-green-50">
                    <CheckCircle className="h-4 w-4 text-green-600" />
                    <AlertDescription className="text-green-700">
                       .   
                    </AlertDescription>
                  </Alert>

                  <FormField
                    control={passwordForm.control}
                    name="password"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel> </FormLabel>
                        <FormControl>
                          <Input
                            type="password"
                            placeholder=" 8 "
                            disabled={isLoading}
                            {...field}
                          />
                        </FormControl>
                        <FormDescription>, ,   </FormDescription>
                        <FormMessage />
                      </FormItem>
                    )}
                  />

                  <FormField
                    control={passwordForm.control}
                    name="confirmPassword"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel> </FormLabel>
                        <FormControl>
                          <Input
                            type="password"
                            placeholder="  "
                            disabled={isLoading}
                            {...field}
                          />
                        </FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />

                  <Button type="submit" className="w-full" disabled={isLoading}>
                    {isLoading && <LoadingSpinner size="sm" className="mr-2" />}
                     
                  </Button>
                </form>
              </Form>
            )}

            {/* Step 4: Success */}
            {step === 'success' && (
              <div className="space-y-6 text-center">
                <div className="mx-auto flex h-16 w-16 items-center justify-center rounded-full bg-green-100">
                  <CheckCircle className="h-8 w-8 text-green-600" />
                </div>
                <div className="space-y-2">
                  <h3 className="text-lg font-semibold"> </h3>
                  <p className="text-muted-foreground text-sm">
                        
                  </p>
                </div>
                <Button onClick={() => router.push('/auth/signin')} className="w-full">
                   
                </Button>
              </div>
            )}

            {/* Back to login link */}
            {step !== 'success' && (
              <div className="mt-6 text-center">
                <p className="text-muted-foreground text-sm">
                   ?{' '}
                  <a href="/auth/signin" className="text-primary hover:underline">
                    
                  </a>
                </p>
              </div>
            )}
          </CardContent>
        </Card>

        {/* Back to home */}
        {step === 'contact' && (
          <div className="mt-6 text-center">
            <Button
              variant="ghost"
              onClick={() => router.push('/')}
              className="text-muted-foreground hover:text-foreground"
            >
              <ArrowLeft className="mr-2 h-4 w-4" />
               
            </Button>
          </div>
        )}
      </div>
    </div>
  );
}
